import axios from "axios";
import React, { useEffect } from "react";
import { useState } from "react";
import { Link } from "react-router-dom";
import { baseUrl2 } from "../../APIURL";
import styles from "./Security.module.scss";

export const Security = ({ currUserId, cookies }) => {
  const [emailFieldShow, setEmailFieldShow] = useState("none");
  const [emailChange, setEmailChange] = useState("");
  const [email, setEmail] = useState("");

  const [passFieldShow, setPassFieldShow] = useState("none");
  const [passChange, setPassChange] = useState("");
  const [pass, setPass] = useState("");

  const [phoneNumberFieldShow, setPhoneNumberFieldShow] = useState("none");
  const [phoneNumberChange, setPhoneNumberChange] = useState("");
  const [phoneNumber, setPhoneNumber] = useState("");

  const onChangeEmailField = (event) => {
    setEmailChange(event.target.value);
  };

  const onChangePassField = (event) => {
    setPassChange(event.target.value);
  };

  const onChangePhoneNumber = (event) => {
    setPhoneNumberChange(event.target.value);
  };

  useEffect(() => {
    (async () => {
      try {
        const data = await axios({
          method: "get",
          url: baseUrl2 + `/users`,
        });

        setEmail(data.data[cookies.id - 1]["email"]);
        setPass(data.data[cookies.id - 1]["password"]);
        setPhoneNumber(data.data[cookies.id - 1]["phoneNumber"]);
      } catch (error) {
        console.error(error);
      }
    })();
  }, [currUserId, cookies.id]);

  const editPass = async () => {
    switch (passFieldShow) {
      case "none": {
        setPassFieldShow("");
        break;
      }
      case "": {
        await axios({
          method: "put",
          url: baseUrl2 + `/users/${currUserId}`,
          data: {
            password: passChange,
          },
        });
        console.log("Пароль успешно изменён");
        setPassFieldShow("none");
        break;
      }
      default: {
        break;
      }
    }
  };

  const editEmail = async () => {
    switch (emailFieldShow) {
      case "none": {
        setEmailFieldShow("");
        break;
      }
      case "": {
        let validator = require("email-validator");

        if (validator.validate(emailChange)) {
          await axios({
            method: "put",
            url: baseUrl2 + `/users/${currUserId}`,
            data: {
              email: emailChange,
            },
          });
        } else {
          console.log("email is wrong");
        }

        setEmailFieldShow("none");
        break;
      }
      default: {
        break;
      }
    }
  };

  const editPhoneNumber = async () => {
    switch (phoneNumberFieldShow) {
      case "none": {
        setPhoneNumberFieldShow("");
        break;
      }
      case "": {
        if (phoneNumberChange.match("[0-9]{10}")) {
          await axios({
            method: "put",
            url: baseUrl2 + `/users/${currUserId}`,
            data: {
              phoneNumber: phoneNumberChange,
            },
          });
          console.log("Номер телефона успешно изменён");
          setPhoneNumberFieldShow("none");
        } else {
          console.log("Формат номера телефона неверный");
        }
        break;
      }
      default: {
        break;
      }
    }
  };

  return (
    <div className={`content p-30`}>
      <div className={styles.container}>
        <div className={`${styles.panel} `}>
          <span
            className={`${styles.emailFormInput} mt-10`}
            style={{ display: passFieldShow }}
          >
            <input
              placeholder="Новый пароль"
              value={passChange}
              onChange={onChangePassField}
            />
          </span>

          <p className="mt-30" onClick={editPass}>
            <svg
              width="18"
              height="18"
              viewBox="0 0 24 24"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                d="M7 17.0129L11.413 16.9979L21.045 7.4579C21.423 7.0799 21.631 6.5779 21.631 6.0439C21.631 5.5099 21.423 5.0079 21.045 4.6299L19.459 3.0439C18.703 2.2879 17.384 2.2919 16.634 3.0409L7 12.5829V17.0129V17.0129ZM18.045 4.4579L19.634 6.0409L18.037 7.6229L16.451 6.0379L18.045 4.4579ZM9 13.4169L15.03 7.4439L16.616 9.0299L10.587 15.0009L9 15.0059V13.4169Z"
                fill="#424242"
              />
              <path
                d="M5 21H19C20.103 21 21 20.103 21 19V10.332L19 12.332V19H8.158C8.132 19 8.105 19.01 8.079 19.01C8.046 19.01 8.013 19.001 7.979 19H5V5H11.847L13.847 3H5C3.897 3 3 3.897 3 5V19C3 20.103 3.897 21 5 21Z"
                fill="#424242"
              />
            </svg>
            Изменить пароль
          </p>

          <span>
            <svg
              width="18"
              height="18"
              viewBox="0 0 24 24"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                d="M20 20H4C3.46957 20 2.96086 19.7893 2.58579 19.4142C2.21071 19.0391 2 18.5304 2 18V5.913C2.02243 5.39779 2.24301 4.91116 2.61568 4.5547C2.98835 4.19824 3.4843 3.99951 4 4H20C20.5304 4 21.0391 4.21071 21.4142 4.58579C21.7893 4.96086 22 5.46957 22 6V18C22 18.5304 21.7893 19.0391 21.4142 19.4142C21.0391 19.7893 20.5304 20 20 20ZM4 7.868V18H20V7.868L12 13.2L4 7.868ZM4.8 6L12 10.8L19.2 6H4.8Z"
                fill="#1195DE"
              />
            </svg>
            {email}
          </span>

          <span
            className={`${styles.emailFormInput} mt-10`}
            style={{ display: emailFieldShow }}
          >
            <input
              type="email"
              placeholder="Адрес электронной почты"
              value={emailChange}
              onChange={onChangeEmailField}
            />
          </span>

          <p onClick={editEmail}>
            <svg
              width="18"
              height="18"
              viewBox="0 0 24 24"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                d="M7 17.0129L11.413 16.9979L21.045 7.4579C21.423 7.0799 21.631 6.5779 21.631 6.0439C21.631 5.5099 21.423 5.0079 21.045 4.6299L19.459 3.0439C18.703 2.2879 17.384 2.2919 16.634 3.0409L7 12.5829V17.0129V17.0129ZM18.045 4.4579L19.634 6.0409L18.037 7.6229L16.451 6.0379L18.045 4.4579ZM9 13.4169L15.03 7.4439L16.616 9.0299L10.587 15.0009L9 15.0059V13.4169Z"
                fill="#424242"
              />
              <path
                d="M5 21H19C20.103 21 21 20.103 21 19V10.332L19 12.332V19H8.158C8.132 19 8.105 19.01 8.079 19.01C8.046 19.01 8.013 19.001 7.979 19H5V5H11.847L13.847 3H5C3.897 3 3 3.897 3 5V19C3 20.103 3.897 21 5 21Z"
                fill="#424242"
              />
            </svg>
            Изменить почту
          </p>
          <span>
            <svg
              width="18"
              height="17  "
              viewBox="0 0 27 26"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                d="M3 26H3.17C22.82 24.87 25.61 8.29 26 3.23C26.0314 2.83658 25.9849 2.44082 25.8631 2.06541C25.7413 1.69 25.5467 1.3423 25.2903 1.04225C25.0339 0.7422 24.7208 0.495688 24.369 0.316839C24.0172 0.137991 23.6335 0.0303218 23.24 9.37591e-07H17.73C17.3294 -0.000386826 16.938 0.119511 16.6064 0.344163C16.2747 0.568815 16.0182 0.88787 15.87 1.26L14.35 5C14.2036 5.36355 14.1673 5.76208 14.2456 6.14609C14.3238 6.5301 14.5131 6.88267 14.79 7.16L16.92 9.31C16.5873 11.2007 15.6818 12.9437 14.3261 14.303C12.9704 15.6622 11.2299 16.5723 9.34 16.91L7.17 14.76C6.8885 14.4862 6.53261 14.3013 6.14672 14.2283C5.76082 14.1554 5.362 14.1977 5 14.35L1.23 15.86C0.863477 16.0129 0.55077 16.2714 0.331718 16.6027C0.112667 16.9339 -0.00281269 17.3229 -3.8147e-06 17.72V23C-3.8147e-06 23.7956 0.316066 24.5587 0.878675 25.1213C1.44128 25.6839 2.20435 26 3 26ZM23 2C23.2652 2 23.5196 2.10536 23.7071 2.29289C23.8946 2.48043 24 2.73478 24 3V3.08C23.54 9 20.59 23 3.06 24C2.92862 24.0081 2.79694 23.9902 2.67249 23.9473C2.54805 23.9045 2.4333 23.8374 2.33482 23.7501C2.23634 23.6628 2.15607 23.5568 2.09861 23.4384C2.04115 23.32 2.00764 23.1914 2 23.06V17.72L5.77 16.21L8.64 19.06L9.12 19C17.82 17.91 19 9.21 19 9.12L19.06 8.64L16.22 5.77L17.72 2H23Z"
                fill="#1195DE"
              />
            </svg>
            {phoneNumber}
          </span>

          <span
            className={`${styles.emailFormInput} mt-10`}
            style={{ display: phoneNumberFieldShow }}
          >
            <input
              placeholder="Новый телефон"
              value={phoneNumberChange}
              onChange={onChangePhoneNumber}
            />
          </span>

          <p onClick={editPhoneNumber}>
            <svg
              width="18"
              height="18"
              viewBox="0 0 24 24"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                d="M7 17.0129L11.413 16.9979L21.045 7.4579C21.423 7.0799 21.631 6.5779 21.631 6.0439C21.631 5.5099 21.423 5.0079 21.045 4.6299L19.459 3.0439C18.703 2.2879 17.384 2.2919 16.634 3.0409L7 12.5829V17.0129V17.0129ZM18.045 4.4579L19.634 6.0409L18.037 7.6229L16.451 6.0379L18.045 4.4579ZM9 13.4169L15.03 7.4439L16.616 9.0299L10.587 15.0009L9 15.0059V13.4169Z"
                fill="#424242"
              />
              <path
                d="M5 21H19C20.103 21 21 20.103 21 19V10.332L19 12.332V19H8.158C8.132 19 8.105 19.01 8.079 19.01C8.046 19.01 8.013 19.001 7.979 19H5V5H11.847L13.847 3H5C3.897 3 3 3.897 3 5V19C3 20.103 3.897 21 5 21Z"
                fill="#424242"
              />
            </svg>
            Изменить телефон
          </p>
          <Link to="/profile">
            <button className={`${styles.blueButton} mt-30`}>
              <svg
                className="mr-20"
                width="16"
                height="14"
                viewBox="0 0 16 14"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  d="M14.7144 7L1.00007 7"
                  stroke="white"
                  strokeWidth="2"
                  strokeLinecap="round"
                  strokeLinejoin="round"
                />
                <path
                  d="M7 13L1 7L7 1"
                  stroke="white"
                  strokeWidth="2"
                  strokeLinecap="round"
                  strokeLinejoin="round"
                />
              </svg>
              Вернуться назад
            </button>
          </Link>
        </div>
      </div>
    </div>
  );
};
