import React, { useEffect, useState } from "react";
import { Card } from "../Card/Card";
import { baseUrl } from "../../APIURL";
import axios from "axios";
import sadEmoji from "../../assets/images/sad-emoji.svg";
import { Link } from "react-router-dom";
import styles from "./Orders.module.scss";

export const Orders = () => {
  const [isLoading, setIsLoading] = useState(true);
  const [orders, setOrders] = useState([]);

  useEffect(() => {
    (async () => {
      try {
        const { data } = await axios.get(baseUrl + "/orders");

        setOrders(data.reduce((prev, obj) => [...prev, ...obj.items], []));
        setIsLoading(false);
      } catch (error) {
        alert("Ошибка при запросе списка заказов");
        console.error(error);
      }
    })();
  }, []);

  return (
    <div className={`${styles.content} p-40`}>
      {orders.length > 0 ? (
        <div>
          <div className="d-flex align-center justify-between mb-40">
            <h1>Мои заказы</h1>
          </div>
          <div className="d-flex flex-wrap">
            {(isLoading ? [...Array(10)] : orders).map((item, index) => (
              <Card key={index} {...item} loading={isLoading} />
            ))}
          </div>
        </div>
      ) : (
        <div className={styles.ordersPage}>
          <img src={sadEmoji} alt="" width={70} height={70} className="mb-25" />
          <h1>У вас нет заказов</h1>
          <p className="opacity-6">
            Эта страница не будет пуста, если вы приобретете хотя бы один товар
          </p>
          <Link to="/profile">
            <button className={`${styles.blueButton} mt-40`}>
              <svg
                className="mr-20"
                width="16"
                height="14"
                viewBox="0 0 16 14"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  d="M14.7144 7L1.00007 7"
                  stroke="white"
                  strokeWidth="2"
                  strokeLinecap="round"
                  strokeLinejoin="round"
                />
                <path
                  d="M7 13L1 7L7 1"
                  stroke="white"
                  strokeWidth="2"
                  strokeLinecap="round"
                  strokeLinejoin="round"
                />
              </svg>
              Вернуться назад
            </button>
          </Link>
        </div>
      )}
    </div>
  );
};
