import React, { useContext } from "react";
import { Card } from "../Card/Card";
import { AppContext } from "../../context";
import cuteEmoji from "./../../assets/images/cute-emoji.svg";
import { Link } from "react-router-dom";
import styles from "./Favorites.module.scss";

export const Favorites = () => {
  const { favorites, onAddToFavorite } = useContext(AppContext);

  return (
    <div className={`${styles.content} p-40`}>
      {favorites.length > 0 ? (
        <div>
          <div className="d-flex align-center justify-between mb-40">
            <h1>Мои закладки</h1>
          </div>

          <div className="d-flex flex-wrap">
            {favorites.map((item, index) => (
              <Card
                key={index}
                favorited={true}
                onFavorite={onAddToFavorite}
                {...item}
              />
            ))}
          </div>
        </div>
      ) : (
        <div className={styles.ordersPage}>
          <img
            src={cuteEmoji}
            alt=""
            width={70}
            height={70}
            className="mb-25"
          />
          <h1>Закладок нет :(</h1>
          <p className="opacity-6">Вы ничего не добавили в закладки</p>
          <Link to="/React-PCSHOP/">
            <button className={`${styles.blueButton} mt-40`}>
              <svg
                className="mr-20"
                width="16"
                height="14"
                viewBox="0 0 16 14"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  d="M14.7144 7L1.00007 7"
                  stroke="white"
                  strokeWidth="2"
                  strokeLinecap="round"
                  strokeLinejoin="round"
                />
                <path
                  d="M7 13L1 7L7 1"
                  stroke="white"
                  strokeWidth="2"
                  strokeLinecap="round"
                  strokeLinejoin="round"
                />
              </svg>
              Вернуться назад
            </button>
          </Link>
        </div>
      )}
    </div>
  );
};
