import axios from "axios";
import React, { useState } from "react";
import { Link, Navigate } from "react-router-dom";
import { baseUrl2 } from "../../APIURL";
import styles from "./Registration.module.scss";

export const Registration = ({
  registerLogin,
  setRegisterLogin,
  setRegisterEmail,
  registerPass,
  setRegisterPass,
  registerSecPass,
  setRegisterSecPass,
  isUserOnline,
  registerEmail,
  phoneNumber,
}) => {
  const [passType, setPassType] = useState("password");
  const [secPassType, setSecPassType] = useState("password");
  const [registerStatus, setRegisterStatus] = useState("");
  const [isRegPassChanged, setIsRegPassChanged] = useState(false);
  const [isRegLoginChanged, setIsRegLoginChanged] = useState(false);
  const [isRegEmailChanged, setIsRegEmailChanged] = useState(false);
  const [isRegSecPassChanged, setIsRegSecPassChanged] = useState(false);
  const [regStatus, setRegStatus] = useState(false);

  const emptyAvatar =
    "https://res.cloudinary.com/virtuux/image/upload/v1655279113/202-2022289_web-reconceptualization-and-redesign-of-carnet-jove-android_jtue3d.png";

  const onChangeLoginRegister = (event) => {
    setIsRegLoginChanged(true);
    setRegisterLogin(event.target.value);
  };

  const onChangeEmailRegister = (event) => {
    setIsRegEmailChanged(true);
    setRegisterEmail(event.target.value);
  };

  const onChangePassRegister = (event) => {
    setIsRegPassChanged(true);
    setRegisterPass(event.target.value);
  };

  const onChangeSecPassRegister = (event) => {
    setIsRegSecPassChanged(true);
    setRegisterSecPass(event.target.value);
  };

  const toggleSecPassType = () => {
    if (secPassType === "text") {
      setSecPassType("password");
    } else if (secPassType === "password") {
      setSecPassType("text");
    }
  };

  const togglePassType = () => {
    if (passType === "text") {
      setPassType("password");
    } else if (passType === "password") {
      setPassType("text");
    }
  };

  const checkPass = async () => {
    setIsRegLoginChanged(false);
    setIsRegEmailChanged(false);
    setIsRegPassChanged(false);
    setIsRegSecPassChanged(false);

    if (
      (registerPass && registerSecPass && registerLogin && registerEmail) !== ""
    ) {
      if (registerPass === registerSecPass) {
        await axios.post(baseUrl2 + "/users", {
          login: registerLogin,

          password: registerPass,
          email: registerEmail,
          phoneNumber: phoneNumber,
          status: isUserOnline,
          avatarUrlProfile: emptyAvatar,
        });

        console.log("Успешная регистрация");
        setRegisterStatus("successful");

        setRegisterLogin(registerLogin);
        setRegisterEmail(registerEmail);
        setRegisterPass(registerPass);

        setRegStatus(true);
      } else {
        console.log("Пароли не совпадают");
        setRegisterStatus("wrong");
      }
    } else {
      console.log("Заполните все поля");
      setRegisterStatus("emptyField");
      setRegStatus(false);
    }
  };

  return (
    <div className={`${styles.content} p-40"`}>
      <div className={styles.container} style={{ height: "500px" }}>
        <h1
          className="d-flex flex-wrap flex-column  mb-30 align-center"
          style={{ color: "#0F98DA" }}
        >
          Регистрация
        </h1>

        <div
          className={`d-flex flex-wrap ${styles.loginFormInput} align-center`}
        >
          <input
            placeholder="Логин"
            value={registerLogin}
            onChange={onChangeLoginRegister}
          />
          <input
            placeholder="Адрес электронной почты"
            value={registerEmail}
            onChange={onChangeEmailRegister}
          />
          <div className="d-flex align-center ">
            <input
              type={passType}
              placeholder="Пароль"
              value={registerPass}
              onChange={onChangePassRegister}
            />
            {passType === "password" ? (
              <svg
                onClick={togglePassType}
                className="eye"
                width="20"
                height="20"
                viewBox="0 0 16 16"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  d="M7.99996 6.00299C8.35019 6.00299 8.697 6.07198 9.02057 6.20601C9.34415 6.34003 9.63816 6.53648 9.88581 6.78414C10.1335 7.03179 10.3299 7.3258 10.4639 7.64938C10.598 7.97295 10.667 8.31976 10.667 8.66999C10.667 9.02023 10.598 9.36703 10.4639 9.69061C10.3299 10.0142 10.1335 10.3082 9.88581 10.5558C9.63816 10.8035 9.34415 10.9999 9.02057 11.134C8.697 11.268 8.35019 11.337 7.99996 11.337C7.29262 11.337 6.61426 11.056 6.1141 10.5558C5.61394 10.0557 5.33296 9.37732 5.33296 8.66999C5.33296 7.96266 5.61394 7.2843 6.1141 6.78414C6.61426 6.28398 7.29262 6.00299 7.99996 6.00299V6.00299ZM7.99996 7.00299C7.55784 7.00299 7.13383 7.17862 6.82121 7.49125C6.50859 7.80387 6.33296 8.22788 6.33296 8.66999C6.33296 9.11211 6.50859 9.53612 6.82121 9.84874C7.13383 10.1614 7.55784 10.337 7.99996 10.337C8.44207 10.337 8.86608 10.1614 9.1787 9.84874C9.49133 9.53612 9.66696 9.11211 9.66696 8.66999C9.66696 8.22788 9.49133 7.80387 9.1787 7.49125C8.86608 7.17862 8.44207 7.00299 7.99996 7.00299ZM7.99996 3.66699C11.076 3.66699 13.73 5.76699 14.467 8.70999C14.4846 8.77426 14.4892 8.84139 14.4805 8.90746C14.4718 8.97352 14.45 9.03718 14.4163 9.0947C14.3827 9.15222 14.3379 9.20243 14.2845 9.24238C14.2312 9.28233 14.1704 9.31122 14.1058 9.32735C14.0411 9.34348 13.9739 9.34653 13.9081 9.33631C13.8422 9.32609 13.7791 9.30281 13.7224 9.26784C13.6656 9.23287 13.6165 9.18691 13.5778 9.13267C13.5391 9.07843 13.5116 9.017 13.497 8.95199C13.1875 7.72827 12.4786 6.64278 11.4825 5.8675C10.4864 5.09222 9.26014 4.67148 7.9979 4.67194C6.73566 4.6724 5.50966 5.09403 4.51414 5.87003C3.51861 6.64604 2.81049 7.73204 2.50196 8.95599C2.46897 9.08379 2.38679 9.19337 2.27333 9.26081C2.15988 9.32826 2.02435 9.34812 1.89632 9.31604C1.76829 9.28397 1.65813 9.20257 1.58987 9.0896C1.52162 8.97663 1.5008 8.84125 1.53196 8.71299C1.89365 7.27145 2.72651 5.99203 3.89832 5.07785C5.07013 4.16366 6.51373 3.6671 7.99996 3.66699Z"
                  fill="#0F98DA"
                />
              </svg>
            ) : (
              <svg
                onClick={togglePassType}
                width="20"
                height="20"
                viewBox="0 0 24 24"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  d="M7.184 11.318L4.5 15M4.5 8C7.5 14.5 16.5 14.5 19.5 8H4.5ZM16.816 11.318L19.5 15L16.816 11.318ZM12 12.875V16.5V12.875Z"
                  stroke="#0F98DA"
                  strokeWidth="1.5"
                  strokeLinecap="round"
                  strokeLinejoin="round"
                />
              </svg>
            )}
          </div>
          <div className="d-flex align-center">
            <input
              type={secPassType}
              placeholder="Повторите пароль"
              value={registerSecPass}
              onChange={onChangeSecPassRegister}
            />
            {secPassType === "password" ? (
              <svg
                onClick={toggleSecPassType}
                className="eye"
                width="20"
                height="20"
                viewBox="0 0 16 16"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  d="M7.99996 6.00299C8.35019 6.00299 8.697 6.07198 9.02057 6.20601C9.34415 6.34003 9.63816 6.53648 9.88581 6.78414C10.1335 7.03179 10.3299 7.3258 10.4639 7.64938C10.598 7.97295 10.667 8.31976 10.667 8.66999C10.667 9.02023 10.598 9.36703 10.4639 9.69061C10.3299 10.0142 10.1335 10.3082 9.88581 10.5558C9.63816 10.8035 9.34415 10.9999 9.02057 11.134C8.697 11.268 8.35019 11.337 7.99996 11.337C7.29262 11.337 6.61426 11.056 6.1141 10.5558C5.61394 10.0557 5.33296 9.37732 5.33296 8.66999C5.33296 7.96266 5.61394 7.2843 6.1141 6.78414C6.61426 6.28398 7.29262 6.00299 7.99996 6.00299V6.00299ZM7.99996 7.00299C7.55784 7.00299 7.13383 7.17862 6.82121 7.49125C6.50859 7.80387 6.33296 8.22788 6.33296 8.66999C6.33296 9.11211 6.50859 9.53612 6.82121 9.84874C7.13383 10.1614 7.55784 10.337 7.99996 10.337C8.44207 10.337 8.86608 10.1614 9.1787 9.84874C9.49133 9.53612 9.66696 9.11211 9.66696 8.66999C9.66696 8.22788 9.49133 7.80387 9.1787 7.49125C8.86608 7.17862 8.44207 7.00299 7.99996 7.00299ZM7.99996 3.66699C11.076 3.66699 13.73 5.76699 14.467 8.70999C14.4846 8.77426 14.4892 8.84139 14.4805 8.90746C14.4718 8.97352 14.45 9.03718 14.4163 9.0947C14.3827 9.15222 14.3379 9.20243 14.2845 9.24238C14.2312 9.28233 14.1704 9.31122 14.1058 9.32735C14.0411 9.34348 13.9739 9.34653 13.9081 9.33631C13.8422 9.32609 13.7791 9.30281 13.7224 9.26784C13.6656 9.23287 13.6165 9.18691 13.5778 9.13267C13.5391 9.07843 13.5116 9.017 13.497 8.95199C13.1875 7.72827 12.4786 6.64278 11.4825 5.8675C10.4864 5.09222 9.26014 4.67148 7.9979 4.67194C6.73566 4.6724 5.50966 5.09403 4.51414 5.87003C3.51861 6.64604 2.81049 7.73204 2.50196 8.95599C2.46897 9.08379 2.38679 9.19337 2.27333 9.26081C2.15988 9.32826 2.02435 9.34812 1.89632 9.31604C1.76829 9.28397 1.65813 9.20257 1.58987 9.0896C1.52162 8.97663 1.5008 8.84125 1.53196 8.71299C1.89365 7.27145 2.72651 5.99203 3.89832 5.07785C5.07013 4.16366 6.51373 3.6671 7.99996 3.66699Z"
                  fill="#0F98DA"
                />
              </svg>
            ) : (
              <svg
                onClick={toggleSecPassType}
                width="20"
                height="20"
                viewBox="0 0 24 24"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  d="M7.184 11.318L4.5 15M4.5 8C7.5 14.5 16.5 14.5 19.5 8H4.5ZM16.816 11.318L19.5 15L16.816 11.318ZM12 12.875V16.5V12.875Z"
                  stroke="#0F98DA"
                  stroke-width="1.5"
                  stroke-linecap="round"
                  stroke-linejoin="round"
                />
              </svg>
            )}
          </div>
        </div>
        <div className="d-flex flex-wrap flex-column  align-center">
          {regStatus === true ? (
            <Navigate to="/login" />
          ) : (
            <button
              className={styles.blueButton}
              style={{ width: "300px", fontSize: "20px" }}
              onClick={(obj) => checkPass(obj)}
            >
              Зарегистрироваться
            </button>
          )}

          <Link to="/login">
            <input
              type={"submit"}
              value={"У меня уже есть аккаунт"}
              className={styles.register}
              style={{ width: "200px" }}
            />
          </Link>

          {registerStatus === "wrong" && (
            <>
              <div
                className={`${styles.overlay} ${
                  registerStatus === "wrong" && styles.overlayVisible
                }`}
              >
                <p
                  className={`${styles.cont}
                  ${
                    isRegPassChanged || isRegLoginChanged
                      ? styles.hide
                      : styles.overlayVisible
                  } `}
                >
                  Пароли не совпадают
                </p>
              </div>
            </>
          )}

          {registerStatus === "emptyField" && (
            <>
              <div
                className={`${styles.overlay} ${
                  registerStatus === "emptyField" && styles.overlayVisible
                }`}
              >
                <p
                  className={`${styles.cont}
                  ${
                    isRegPassChanged || isRegLoginChanged || isRegSecPassChanged
                      ? styles.hide
                      : styles.overlayVisible
                  } `}
                >
                  Заполните все поля
                </p>
              </div>
            </>
          )}
        </div>
      </div>
    </div>
  );
};
