import React, { useEffect, useState } from "react";
import { Link, Navigate, useNavigate } from "react-router-dom";
import { baseUrl2 } from "../../APIURL";
import styles from "./Profile.module.scss";
import axios from "axios";
import loader from "../../assets/gifs/loader.gif";
import { Loader } from "../Loader/Loader";

export const Profile = ({ isAuth, currUserId, cookies }) => {
  const [imageSelected, setImageSelected] = useState();
  const [avatarUrlProfile, setAvatarUrlProfile] = useState(loader);
  const [btnShow, setBtnShow] = useState("none");
  const navigate = useNavigate();

  useEffect(() => {
    (async () => {
      try {
        if (cookies.id !== undefined) {
          const data = await axios({
            method: "get",
            url: baseUrl2 + `/users/${cookies.id}`,
          });
          setAvatarUrlProfile(data.data.avatarUrlProfile);
        }
      } catch (error) {
        console.error(error);
      }
    })();
  }, [cookies.id]);

  const uploadImage = async () => {
    const formData = new FormData();
    formData.append("file", imageSelected);
    formData.append("upload_preset", "dfpgireh");

    let response = await axios.post(
      "https://api.cloudinary.com/v1_1/virtuux/image/upload",
      formData
    );
    setAvatarUrlProfile(response.data.secure_url);

    if (currUserId !== undefined) {
      await axios({
        method: "put",
        url: baseUrl2 + `/users/${currUserId}`,
        data: {
          avatarUrlProfile: response.data.secure_url,
        },
      });
    }
    setBtnShow("none");
  };

  const addImages = (event) => {
    setImageSelected(event.target.files[0]);
    setBtnShow("");
  };

console.log(isAuth)

  if(isAuth === false){
    navigate("/login")
  }

  return (
    <div className={`content p-40`}>
      <div className={styles.container}>
        {isAuth && (
          <div className={`${styles.panel} `}>
            <img
              src={avatarUrlProfile}
              alt=""
              width={200}
              height={200}
              className="mt-30"
            />
            <div className={styles.fileInput}>
              <svg
                width="18"
                height="18"
                viewBox="0 0 24 24"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  d="M7 17.0129L11.413 16.9979L21.045 7.4579C21.423 7.0799 21.631 6.5779 21.631 6.0439C21.631 5.5099 21.423 5.0079 21.045 4.6299L19.459 3.0439C18.703 2.2879 17.384 2.2919 16.634 3.0409L7 12.5829V17.0129V17.0129ZM18.045 4.4579L19.634 6.0409L18.037 7.6229L16.451 6.0379L18.045 4.4579ZM9 13.4169L15.03 7.4439L16.616 9.0299L10.587 15.0009L9 15.0059V13.4169Z"
                  fill="#424242"
                />
                <path
                  d="M5 21H19C20.103 21 21 20.103 21 19V10.332L19 12.332V19H8.158C8.132 19 8.105 19.01 8.079 19.01C8.046 19.01 8.013 19.001 7.979 19H5V5H11.847L13.847 3H5C3.897 3 3 3.897 3 5V19C3 20.103 3.897 21 5 21Z"
                  fill="#424242"
                />
              </svg>
              <input
                type="file"
                id="file"
                className={styles.file}
                onChange={addImages}
              />
              <label htmlFor="file">Изменить аватар</label>
            </div>
            <div style={{ display: btnShow }}>
              <p onClick={uploadImage} className={styles.confirmBtn}>
                <svg
                  width="16"
                  height="15"
                  viewBox="0 0 27 26"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    d="M25.548 1.043C25.2911 0.838583 24.9964 0.686776 24.6808 0.596248C24.3652 0.505719 24.0349 0.478242 23.7087 0.515386C23.3824 0.55253 23.0667 0.653567 22.7796 0.812728C22.4924 0.971889 22.2394 1.18606 22.035 1.443L12 14.067L8.492 9.653C8.07932 9.13384 7.47731 8.79988 6.8184 8.72458C6.15949 8.64929 5.49766 8.83882 4.9785 9.2515C4.45934 9.66418 4.12538 10.2662 4.05008 10.9251C3.97478 11.584 4.16432 12.2458 4.577 12.765L10.042 19.64C10.516 20.237 11.237 20.583 11.999 20.583C12.761 20.583 13.481 20.233 13.956 19.639L25.95 4.555C26.81 3.475 26.63 1.901 25.548 1.042V1.043ZM20.5 22.5H3.5V5.5H16.256L18.641 2.5H2C1.17 2.5 0.5 3.17 0.5 4V24C0.5 24.828 1.17 25.5 2 25.5H22C22.3978 25.5 22.7794 25.342 23.0607 25.0607C23.342 24.7794 23.5 24.3978 23.5 24V10.85L20.5 14.624V22.5Z"
                    fill="#424242"
                  />
                </svg>
                Подтвердить
              </p>
            </div>
            <Link to="/security">
              <p>
                <svg
                  width="14"
                  height="18"
                  viewBox="0 0 18 22"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    d="M9 0L0 4V10C0 15.55 3.84 20.74 9 22C14.16 20.74 18 15.55 18 10V4L9 0ZM9 10.99H16C15.47 15.11 12.72 18.78 9 19.93V11H2V5.3L9 2.19V10.99Z"
                    fill="#424242"
                  />
                </svg>
                Безопасность
              </p>
            </Link>
            <Link to="/orders">
              <p className="mb-30">
                <svg
                  width="14"
                  height="14"
                  viewBox="0 0 20 20"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    d="M10.586 0.586C10.211 0.210901 9.70239 0.000113275 9.172 0H2C1.46957 0 0.960859 0.210714 0.585786 0.585786C0.210714 0.960859 0 1.46957 0 2V9.172C0.000113275 9.70239 0.210901 10.211 0.586 10.586L8.586 18.586C8.96106 18.9609 9.46967 19.1716 10 19.1716C10.5303 19.1716 11.0389 18.9609 11.414 18.586L18.586 11.414C18.9609 11.0389 19.1716 10.5303 19.1716 10C19.1716 9.46967 18.9609 8.96106 18.586 8.586L10.586 0.586V0.586ZM5 7C4.46943 6.99987 3.96065 6.78897 3.58558 6.41371C3.21051 6.03845 2.99987 5.52957 3 4.999C3.00013 4.46843 3.21103 3.95965 3.58629 3.58458C3.96155 3.20951 4.47043 2.99887 5.001 2.999C5.53157 2.99913 6.04035 3.21003 6.41542 3.58529C6.79049 3.96055 7.00113 4.46943 7.001 5C7.00087 5.53057 6.78997 6.03935 6.41471 6.41442C6.03945 6.78949 5.53057 7.00013 5 7V7Z"
                    fill="#424242"
                  />
                </svg>
                Мои покупки
              </p>
            </Link>
          </div>
        )}
      </div>
    </div>
  );
};
