import React, { useEffect, useState } from "react";
import { Drawer } from "./components/Drawer/Drawer";
import { Header } from "./components/Header/Header";
import { Navigate, Route, Routes, useNavigate } from "react-router-dom";
import { Favorites } from "./components/Favorites/Favorites";
import { AppContext } from "./context";
import { baseUrl, baseUrl2 } from "./APIURL";
import { Orders } from "./components/Orders/Orders";
import { Home } from "./components/Home/Home";
import { NotFound } from "./components/NotFound/NotFound";
import { Profile } from "./components/Profile/Profile";
import { Registration } from "./components/Registration/Registration";
import { Login } from "./components/Login/Login";
import { Security } from "./components/Security/Security";
import { useCookies } from "react-cookie";
import axios from "axios";
import loader from "./assets/gifs/loader.gif";
import emptyAvatar from "./assets/images/emptyAvatar.svg";

export const App = () => {
  const [items, setItems] = useState([]);
  const [cartItems, setCartItems] = useState([]);
  const [favorites, setFavorites] = useState([]);
  const [searchValue, setSearchValue] = useState("");
  const [cartOpened, setCartOpened] = useState(false);
  const [isLoading, setIsLoading] = useState(true);
  const [registerLogin, setRegisterLogin] = useState("");
  const [registerEmail, setRegisterEmail] = useState("");
  const [registerPass, setRegisterPass] = useState("");
  const [registerSecPass, setRegisterSecPass] = useState("");
  const [login, setLogin] = useState("");
  const [pass, setPass] = useState("");
  const [passType, setPassType] = useState("password");
  const [isAuth, setIsAuth] = useState(false);
  const [loginAuthStatus, setLoginAuthStatus] = useState("");
  const [isUserOnline, setIsUserOnline] = useState(false);
  const [userName, setUserName] = useState("");
  const [email, setEmail] = useState("");
  const [isPassChanged, setIsPassChanged] = useState(false);
  const [isLoginChanged, setIsLoginChanged] = useState(false);
  const [avatarUrlProfile, setAvatarUrlProfile] = useState(loader);
  const [currUserId, setCurrUserId] = useState(0);
  const [cookies, setCookie, removeCookie] = useCookies([""]);

  useEffect(() => {
    (async () => {
      const dataStore = [];
      const ids = [];
      const logins = [];
      const emails = [];

      try {
        if (cookies.id !== undefined) {
          const data = await axios({
            method: "get",
            url: baseUrl2 + `/users`,
          });
          for (let index = 0; index < data.data.length; index++) {
            let dataLogin = data.data[index]["login"];
            let dataStatus = data.data[index]["status"];
            let dataId = data.data[index]["id"];
            let dataEmail = data.data[index]["email"];

            logins.push(dataLogin);
            dataStore.push(dataStatus);
            ids.push(dataId);
            emails.push(dataEmail);
          }
          setCurrUserId(cookies.id);
          setEmail(data.data[Number(cookies.id) - 1]["email"]);
          setIsUserOnline(true);
          setUserName(cookies.login);

          await axios({
            method: "put",
            url: baseUrl2 + `/users/${cookies.id}`,
            data: {
              status: true,
            },
          });

          setLoginAuthStatus("successful");
          console.log("Авторизация успешна");
        }
      } catch (error) {
        console.error(error);
      }
    })();
  }, [cookies.id, cookies.login, currUserId, email, cookies.password]);

  useEffect(() => {
    (async () => {
      try {
        const [cartResponse, favoritesResponse, itemsResponse] =
          await Promise.all([
            axios.get(baseUrl + "/cart"),
            axios.get(baseUrl + "/favorites"),
            axios.get(baseUrl + "/items"),
          ]);

        setIsLoading(false);
        setCartItems(cartResponse.data);
        setFavorites(favoritesResponse.data);
        setItems(itemsResponse.data);
      } catch (error) {
        console.error(error);
      }
    })();
  }, []);

  useEffect(() => {
    (async () => {
      try {
        if (cookies.id !== undefined) {
          const data1 = await axios({
            method: "get",
            url: baseUrl2 + `/users/${cookies.id}`,
          });
          setIsAuth(data1.data.status);
        }
      } catch (error) {
        console.error(error);
      }
    })();
  }, [cookies.id, currUserId]);

  useEffect(() => {
    (async () => {
      if (cookies.id !== undefined) {
        await axios
          .get(baseUrl2 + `/users/${cookies.id}`)
          .then((data) => setAvatarUrlProfile(data.data.avatarUrlProfile));
      }
    })();
  }, [cookies.id, currUserId]);

  useEffect(() => {
    (async () => {
      try {
        const { data } = await axios.get(baseUrl2 + "/users");
        const logins = [];
        const passwords = [];
        const ids = [];

        for (let index = 0; index < data.length; index++) {
          let dataLogin = data[index]["login"];
          let dataPass = data[index]["password"];
          let dataId = data[index]["id"];

          logins.push(dataLogin);
          passwords.push(dataPass);
          ids.push(dataId);
        }
        console.log("cookies.id", cookies.id);
      } catch (error) {
        console.error(error);
      }
    })();
  }, [cookies]);

  const onAddToCart = async (obj) => {
    try {
      const findItem = cartItems.find(
        (item) => Number(item.parentId) === Number(obj.id)
      );

      if (findItem) {
        setCartItems((prev) =>
          prev.filter((item) => Number(item.parentId) !== Number(obj.id))
        );
        await axios.delete(`${baseUrl}/cart/${findItem.id}`);
      } else {
        setCartItems((prev) => [...prev, obj]);
        const { data } = await axios.post(baseUrl + "/cart", obj);

        setCartItems((prev) =>
          prev.map((item) => {
            if (item.parentId === data.parentId) {
              return {
                ...item,
                id: data.id,
              };
            } else {
              return item;
            }
          })
        );
      }
    } catch (error) {
      console.error(error);
    }
  };

  const onRemoveItem = async (id) => {
    try {
      await axios.delete(`${baseUrl}/cart/${id}`);
      setCartItems((prev) =>
        prev.filter((item) => Number(item.id) !== Number(id))
      );
    } catch (error) {
      console.error(error);
    }
  };

  const onAddToFavorite = async (obj) => {
    try {
      if (favorites.find((favObj) => Number(favObj.id) === Number(obj.id))) {
        await axios.delete(`${baseUrl}/favorites/${obj.id}`);

        setFavorites((prev) =>
          prev.filter((item) => Number(item.id) !== Number(obj.id))
        );
      } else {
        await axios.post(baseUrl + "/favorites", obj);
        setFavorites((prev) => [...prev, obj]);
      }
    } catch (error) {
      console.error(error);
    }
  };

  const onChangeSearchInput = (event) => {
    setSearchValue(event.target.value);
  };

  const onChangeLoginRegister = (event) => {
    setRegisterLogin(event.target.value);
  };
  const onChangeEmailRegister = (event) => {
    setRegisterEmail(event.target.value);
  };

  const onChangePassRegister = (event) => {
    setRegisterPass(event.target.value);
  };

  const onChangeSecPassRegister = (event) => {
    setRegisterSecPass(event.target.value);
  };

  const onChangeLogin = (event) => {
    setLogin(event.target.value);
    setIsLoginChanged(true);
  };

  const onChangePass = (event) => {
    setPass(event.target.value);
    setIsPassChanged(true);
  };

  const isItemAdded = (id) => {
    return cartItems.some((obj) => Number(obj.parentId) === Number(id));
  };

  const isItemFavorite = (id) => {
    return favorites.some((obj) => Number(obj.parentId) === Number(id));
  };

  const checkPass = async () => {
    try {
      setIsPassChanged(false);
      setIsLoginChanged(false);

      const { data } = await axios.get(baseUrl2 + "/users");

      const logins = [];
      const passwords = [];
      const ids = [];

      for (let index = 0; index < data.length; index++) {
        let dataLogin = data[index]["login"];
        let dataPass = data[index]["password"];
        let dataId = data[index]["id"];

        logins.push(dataLogin);
        passwords.push(dataPass);
        ids.push(dataId);
      }

      if ((login && pass) === "") {
        console.log("Заполните все поля");
        setLoginAuthStatus("emptyField");
      } else {
        if (cookies.id === undefined) {
          if (
            logins.some((e) => e === login) &&
            passwords.some((e) => e === pass)
          ) {
            setIsAuth(!isAuth);
            setIsUserOnline(!isUserOnline);
            setLoginAuthStatus("successful");
            setCookie("login", login);
            setCookie("password", pass);
            setCookie(
              "id",
              logins.indexOf(logins.find((e) => e === login)) + 1
            );
            setCookie("email", email);

           
          } else {
            console.log("Логин или пароль неверны");
            setLoginAuthStatus("wrong");
          }
        }
      }
    } catch (error) {
      console.log(error);
    }
  };

  

  const handleStateChange = async (isAuthType) => {
    try {
      if (cookies.id !== undefined) {
        removeCookie("login");
        removeCookie("password");
        removeCookie("id");
        removeCookie("email");
        setLoginAuthStatus("logout")
        setIsUserOnline(false);
        setIsAuth(isAuthType);

        await axios({
          method: "put",
          url: baseUrl2 + `/users/${cookies.id}`,
          data: {
            status: false,
          },
        });
      }
    } catch (error) {
      console.error(error);
    }
  };
 
  return (
    <AppContext.Provider
      value={{
        items,
        cartItems,
        favorites,
        isItemAdded,
        isItemFavorite,
        onAddToFavorite,
        onAddToCart,
        setCartOpened,
        setCartItems,
        setFavorites,
      }}
    >
      <div className="wrapper clear">
        <Drawer
          items={cartItems}
          onClose={() => setCartOpened(false)}
          onRemove={onRemoveItem}
          opened={cartOpened}
        />

        <Header
          onClickCart={() => setCartOpened(!cartOpened)}
          isAuth={isAuth}
          handleStateChange={handleStateChange}
          isUserOnline={isUserOnline}
          userName={userName}
          avatarUrlProfile={avatarUrlProfile}
          emptyAvatar={emptyAvatar}
          cookies={cookies}
          
        />
        <Routes>
          <Route
            path="React-PCSHOP"
            element={
              <Home
                items={items}
                cartItems={cartItems}
                searchValue={searchValue}
                setSearchValue={setSearchValue}
                onChangeSearchInput={onChangeSearchInput}
                onAddToFavorite={onAddToFavorite}
                onAddToCart={onAddToCart}
                isLoading={isLoading}
                isUserOnline={isUserOnline}
              />
            }
          />
          <Route exact path="favorites" element={<Favorites />} />
          <Route exact path="orders" element={<Orders />} />
          <Route
            exact
            path="profile"
            element={
              <Profile
                isUserOnline={isUserOnline}
                currUserId={currUserId}
                cookies={cookies}
                isAuth={isAuth}
              />
            }
          />
          <Route
            exact
            path="registration"
            element={
              <Registration
                isUserOnline={isUserOnline}
                registerLogin={registerLogin}
                setRegisterLogin={setRegisterLogin}
                setRegisterEmail={setRegisterEmail}
                registerEmail={registerEmail}
                registerPass={registerPass}
                setRegisterPass={setRegisterPass}
                registerSecPass={registerSecPass}
                setRegisterSecPass={setRegisterSecPass}
                onChangeLoginRegister={onChangeLoginRegister}
                onChangeEmailRegister={onChangeEmailRegister}
                onChangePassRegister={onChangePassRegister}
                onChangeSecPassRegister={onChangeSecPassRegister}
              />
            }
          />
          <Route
            exact
            path="login"
            element={
              <Login
                isLoginChanged={isLoginChanged}
                isPassChanged={isPassChanged}
                checkPass={checkPass}
                onChangeLogin={onChangeLogin}
                onChangePass={onChangePass}
                login={login}
                pass={pass}
                setLogin={setLogin}
                setPass={setPass}
                isAuth={isAuth}
                setPassType={setPassType}
                passType={passType}
                loginAuthStatus={loginAuthStatus}
                setIsUserOnline={setIsUserOnline}
                isUserOnline={isUserOnline}
                cookies={cookies}
              />
            }
          />
          <Route
            path="security"
            element={
              <Security
                email={email}
                currUserId={currUserId}
                cookies={cookies}
              />
            }
          />
          <Route path="*" element={<NotFound />} />
        </Routes>
      </div>
    </AppContext.Provider>
  );
};
